import VeeValidate from 'vee-validate';
import messages from './zh_CN';
VeeValidate.Validator.localize({
    zh_CN: messages
});
const config = {
    errorBagName: 'errors', // change if property conflicts.
    delay: 0,
    locale: 'zh_CN',
    messages: null,
    strict: true
};
export {VeeValidate, config};



